@import '../framework/site.scss';

$about-us-content-max-width: 50rem;

#about-us {
    display: flex;
    flex-flow: row wrap;
    padding-bottom: $offset-huge;

    h5 {
        display: block;
        text-transform: uppercase;
    }

    #online {
        display: flex;
        flex-flow: column;
        flex-basis: 50%;
        align-items: flex-end;
        
        #contact {
            display: flex;
            flex-flow: column;

            input[type='submit'] {
                margin-top: 1rem;
                margin-bottom: 1rem;
            }
        
            h5 {
                flex-basis: 100%;
                margin: $offset-huge;
            }

            > div {
                margin-left: $offset-huge;
                margin-right: $offset-huge;
                max-width: $about-us-content-max-width;
                align-self: center;
            }
        }
    }

    #conventional {
        display: flex;
        flex-flow: column;
        flex-basis: 50%;
        align-self: flex-start;

        #location, #correspondence, #phone, #vcard {
            display: flex;
            flex-flow: row wrap;

            h5 {
                flex-basis: 100%;
                margin: $offset-huge;
            }

            svg {
                margin-left: $offset-huge;

                * {
                    fill: $color-brand-accent-red;
                }
            }

            p, a, address {
                margin: 0 $offset-huge 0 $offset-regular;
            }
        }
        
        #social {
            display: flex;
            flex-flow: row wrap;
            
            h5 {
                flex-basis: 100%;
                margin: $offset-huge;
            }

            svg {
                width: 3.2rem;
                height: 3.2rem;
            }

            a {
                margin-left: $offset-huge;
            }

            a:hover svg * {
                fill: $color-brand-accent-red;
            }

            a ~ a {
                margin-left: $offset-large;
            }

            > :last-child {
                margin-right: $offset-huge;
            }
        }
    }

    #qr {
        flex: 1;
        display: flex;
        flex-flow: column;
        justify-self: center;
        place-items: center;

        h5 {
            margin: $offset-huge;
        }
    }
}

@media (max-width: $breakpoint-tablet) {
    #about-us {
        #online, #conventional {
            flex-basis: 100%;
            align-items: center;

            > * {
                max-width: $about-us-content-max-width + $offset-huge + $offset-huge;
                width: 100%;
            }
        }
    }

    #qr {
        display: none !important;
    }
}

@media (max-width: $breakpoint-mobile) {
    #qr {
        display: none !important;
    }
}