@import '../framework/site.scss';

#innovate {
    padding-left: 20%;
    padding-right: 20%;

    h5 {
        text-transform: uppercase;
        text-align: left;
    }

    section {
        margin-bottom: $offset-huge;
        text-align: justify;

        &.blockquote {
            display: flex;
            place-items: center;
            place-content: center;
        }

        blockquote {
            display: flex;
            flex-flow: row wrap;
            max-width: 80rem;

            img {
                border: 0.3rem solid $color-brand-accent-grey;
                border-radius: 50%;
                width: 8rem;
                height: 8rem;
            }

            > div {
                flex: 1;
                margin-left: $offset-huge;

                > div:not(:first-child) {
                    margin-top: $offset-regular;
                }
            }

            p {
                text-align: right;
                margin-bottom: 0;
            }
        }

        &.cta {
            text-align: center;
        }
    }
}

@media (max-width: $breakpoint-tablet) {
    #innovate {
        padding-left: 10%;
        padding-right: 10%;
    }
}

@media (max-width: $breakpoint-mobile) {
    #innovate {
        padding-left: $offset-huge;
        padding-right: $offset-huge;

        blockquote {
            flex-flow: column !important;
            align-items: center;

            div {
                margin-top: $offset-large;
                margin-left: 0 !important;
            }

            p {
                text-align: right;
            }
        }
    }
}