@import '../framework/site.scss';

#contact-slide {
  display: flex;
  flex-flow: column;
  width: 100%;

  h1 {
    color: $color-brand-white;
    background-color: $color-brand-accent-red;
    padding-left: $offset-large;
    padding-right: $offset-large;
    font-size: 10rem;
    place-self: flex-start;
  }

  p {
    margin: $offset-small 0;
    padding: $offset-regular;
    background-color: $color-light-background;
    color: $color-dark-text;
    place-self: flex-start;
  }

  div {
    flex: 1;
    display: flex;
    flex-flow: row;
    place-content: center;
  }

  a.button.primary.accent {
    place-self: flex-end;
    margin: $offset-huge $offset-regular 7rem $offset-regular;
  }
}

@media (max-width: $breakpoint-tablet) {
  #contact-slide h1 {
    font-size: 7rem;
  }
}

@media (max-width: $breakpoint-mobile) {
  #contact-slide h1 {
    font-size: $font-size-h1;
  }
}