@import '../framework/site.scss';

form {
    display: flex;
    flex-flow: column;

    > * {
        display: flex;
        flex-flow: column;
        padding: $offset-regular 0;

        &:first-child {
            padding-top: 0;
        }
    }

    .form-error {
        margin-top: $offset-small;
    }

    :last-child {
        display: flex;
        flex-flow: row wrap;
        padding: 0;

        label {
            margin-bottom: $offset-regular;
            font-size: $font-size-small;
            flex-basis: 100%;
        }

        #g-recaptcha {
            align-self: flex-start;
            flex: 1;
        }

        input[type="submit"] {
            align-self: center;
            flex: 0;
        }
    }
}

#contact-widget-sent,
#contact-widget-error {
    text-align: center;
    min-width: $breakpoint-mobile - 100px;
    margin: 20rem 0;

    img {
        width: 10rem;
        height: 10rem;
    }
}

@media (max-width: $breakpoint-tablet) {
}

@media (max-width: $breakpoint-mobile) {
    #contact-widget-sent,
    #contact-widget-error {
        min-width: auto !important;
        margin: auto !important;
    }
}