@import '../framework/site.scss';

.jumbotron {
    display: flex;
    position: relative;
    min-height: 100%;
    min-width: 100%;
}

.jumbotron-picker-position {
    position: absolute;
    bottom: 0;
    left: 50%;
}

.jumbotron-picker-container {
    position: relative;
    left: -50%;
}

.jumbotron-switch {
    border: 0.2rem solid $color-brand-accent-red;
    border-radius: 50%;
    padding: 0;
    margin: $offset-large $offset-regular;
    width: $offset-regular;
    height: $offset-regular;
    cursor: pointer;
    outline: none;

    &:not(.selected) {
        background-color: $color-light-background;
    }

    &.selected {
        background-color: $color-brand-accent-red;
    }
}