@import '../framework/site.scss';

$app-footer-height-desktop: 10rem;
$app-footer-height-tablet: 10rem;
$app-footer-height-mobile: 8rem;

footer.app-footer {
    height: $app-footer-height-desktop;
    background: $color-brand-dark-grey;
    color: $color-plain-white;
    display: flex;
    flex-flow: row wrap;
    flex: 0;

    a {
        color: $color-light-text;
    }

    .app-footer-intro {
        @include font-base($font-size: $font-size-small);

        flex: 100%;
    }

    .app-footer-outro {
        @include font-base($font-size: $font-size-small);

        background-color: $color-brand-accent-red;
        padding: $offset-regular;
        justify-content: flex-end;
        white-space: nowrap;

        > * {
            margin: $offset-tiny $offset-small;
        }

        a {
            @include font-base($font-size: $font-size-small);
        }
    }
}

@media (max-width: $breakpoint-tablet) {
    footer.app-footer {
        height: $app-footer-height-tablet;
    }
}

@media (max-width: $breakpoint-mobile) {
    footer.app-footer {
        height: $app-footer-height-mobile;

        .app-footer-outro {
            > * {
                flex: 100% !important;
                text-align: center;
            }
        }
    }
}