@import '../framework/site.scss';

div.app-logo {
    margin: 0 1rem;
    display: flex;
    flex-flow: row;

    img {
        place-self: center;
        width: 5rem;
        height: 5rem;
        margin-left: $offset-large;
    }
}

@media (max-width: $breakpoint-tablet) {
    div.app-logo {
        display: none;
    }
}

@media (max-width: $breakpoint-mobile) {
    div.app-logo {
        display: none;
    }
}