@import './parameters.scss';

div.app-menu-item {
    display: flex;
    align-items: center;
    white-space: nowrap;
    text-transform: uppercase;
    background: $color-app-menu-background-normal;
    
    &:hover {
        background: $color-app-menu-background-hover;
    }

    a {
        height: 100%;
        display: flex;
        flex-flow: row;
        align-items: center;
        text-decoration: none;
        color: $color-app-menu-foreground-normal;
        font-weight: $font-weight-bold;
        padding-left: $offset-large;
        padding-right: $offset-large;

        &:hover {
            text-decoration: none;
            color: $color-app-menu-foreground-hover !important;
        }

        &:visited {
            text-decoration: none;
            color: $color-app-menu-foreground-normal;
        }
    }
}

div.app-menu-desktop, div.app-menu-tablet, div.app-menu-mobile {
    display: flex;
    flex-flow: row;
    align-items: center;
    height: 100%;
}

div.app-menu-desktop, div.app-menu-tablet {
    div.app-menu-item {
        height: 100%;
    }
}

div.app-menu-mobile {
    .app-menu-burger {
        display: flex;
        flex-flow: column;
        width: $app-menu-burger-width;
        height: $app-menu-burger-height;
        color: $app-menu-burger-foreground;
        background-color: $app-menu-burger-background;
        border: none;
    }

    div.app-menu-indicator {
        position: relative;
        margin: 2.2rem 1.5rem;
    }
    
    div.app-menu-expansion {
        display: none;
    }

    span {
        display: block;
        position: absolute;
        height: 0.3rem;
        width: 100%;
        background-color: $color-app-menu-foreground-normal;
        left: 0;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
        -webkit-transition: .25s ease-in-out;
        -moz-transition: .25s ease-in-out;
        -o-transition: .25s ease-in-out;
        transition: .25s ease-in-out;

        &:nth-child(1) {
            top: 0px;
        }

        &:nth-child(2), &:nth-child(3) {
            top: 0.9rem;
        }

        &:nth-child(4) {
            top: 1.8rem;
        }
    }

    &[aria-expanded="true"] {
        span:nth-child(1) {
            top: 0.9rem;
            width: 0%;
            left: 50%;
        }

        span:nth-child(2) {
            -webkit-transform: rotate(45deg);
            -moz-transform: rotate(45deg);
            -o-transform: rotate(45deg);
            transform: rotate(45deg);
        }

        span:nth-child(3) {
            -webkit-transform: rotate(-45deg);
            -moz-transform: rotate(-45deg);
            -o-transform: rotate(-45deg);
            transform: rotate(-45deg);
        }

        span:nth-child(4) {
            top: 0.9rem;
            width: 0%;
            left: 50%;
        }
        
        div.app-menu-expansion {
            display: flex;
            flex-flow: column;
            position: absolute;
            left: 0;
            bottom: 0;
            top: $app-header-height-mobile;
            background: $color-brand-dark-grey;
            width: 100%;
            z-index: 1000;
        }
    }

    div.app-menu-item {
        height: 5rem;
    }
}

@media (min-width: $breakpoint-tablet) {
    div.app-menu-tablet, div.app-menu-mobile {
        display: none !important;
    }
}

@media (min-width: $breakpoint-mobile) and (max-width: $breakpoint-tablet) {
    div.app-menu-desktop, div.app-menu-mobile {
        display: none !important;
    }
}

@media (max-width: $breakpoint-mobile) {
    div.app-menu-desktop, div.app-menu-tablet {
        display: none !important;
    }

    .app-menu-item a {
        width: 100%;
    }
}